import React from 'react';
import {v1} from 'uuid';
import {StoreType} from '../../../Store/Store';
import {Grid, Paper} from '@material-ui/core';

export type CategoryListPropsType = {
    store: StoreType
    category: string
}

export const CategoryList = (props: CategoryListPropsType) => {
    const specialProductsItems = props.store.items.filter(f => f.category === props.category)

    return (
        <Grid container justifyContent={'center'} spacing={1}>
            {specialProductsItems.map(m => {
                return (
                    <Grid item key={m.id}>
                        <Paper elevation={6} style={{padding: '10px'}}>
                            <Grid container alignItems={'center'} justifyContent={'center'}>
                                <Grid item>
                                    <img src={props.store.description.find(f => f.id === m.id)?.picture}
                                         alt={'Изображение'}/>
                                </Grid>
                                <Grid item>
                                    <Grid item style={{width: '250px'}}>
                                        <h3 style={{margin: '0px'}}>
                                            {m.name}
                                        </h3>
                                        <span style={{fontStyle: 'italic'}}>Объём {m.volume} мл.</span>
                                    </Grid>
                                    <Grid container direction={'column'} style={{marginTop: '30px'}}>
                                        <span>Цена - <b>{m.priceRegular} руб.</b></span>
                                        <span>В день процедуры - <b>{m.priceDiscounted} руб.</b></span>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <div>
                                <h4>Действие:</h4>
                                <ul>
                                    {props.store.description.find(f => f.id === m.id)?.action.map(m1 => <li
                                        key={v1()}>{m1}</li>)}
                                </ul>
                                <h4>Ингридиенты:</h4>
                                <ul>
                                    {props.store.description.find(f => f.id === m.id)?.ingredients.map(m2 => <li
                                        key={v1()}>{m2}</li>)}
                                </ul>
                                <h4>Способ применения:</h4>
                                <ul>
                                    {props.store.description.find(f => f.id === m.id)?.apply}
                                </ul>
                            </div>
                        </Paper>
                    </Grid>
                )
            })}
        </Grid>
    )
}